import React, { useState, useEffect } from 'react';
import './App.css';
import Post from './Post';
import { db,auth } from './firebase';
import Modal from '@material-ui/core/Modal';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Input } from '@material-ui/core';
import ImageUpload from './ImageUpload';



function getModalStyles() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const useStyles = makeStyles((theme) => ({
    paper: {
        position: 'absolute',
        width: 400,
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
    },
}));
function App() {
    const classes = useStyles();
    const [modalStyle] = useState(getModalStyles);
    const [posts, setPost] = useState([]);
    const [open, setOpen] = useState(false);
    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [user,setUser] = useState(null);
    const [openSignIn,setOpenSignIn]=useState(false);

    useEffect(()=>{
        const unsubscribe= auth.onAuthStateChanged((authUser)=>{
            if(authUser) {
                console.log(authUser);
                setUser(authUser);
            }else{
                setUser(null);
            }
        })
        return ()=>{
            unsubscribe();
        }
    },[user,username])
    useEffect(() => {
        db.collection('posts').orderBy('timestamp','desc').onSnapshot(snapshot => {
            setPost(snapshot.docs.map(doc => ({
                id: doc.id,
                post: doc.data()
            })));
        });
    }, [posts]);

    const signUp = (event) => {
        event.preventDefault();
        auth
        .createUserWithEmailAndPassword(email,password)
        .then((authUser)=>{
            return authUser.user.updateProfile({
                displayName:username
            })
        })
        .catch((error)=>alert(error.message));
        setOpen(false);
    }

    const signIn= (event)=>{
        event.preventDefault();
        auth
        .signInWithEmailAndPassword(email,password)
        .catch((error)=>alert(error.message))

        setOpenSignIn(false);
    }
    return (
        <div className="app">
            <Modal
                open={open}
                onClose={() => setOpen(false)}
            >
                <div style={modalStyle} className={classes.paper}>
                    <form className="app__signup">
                    <center>
                        <img className="app__headerImage" src="https://cdn.worldvectorlogo.com/logos/instagram-1.svg" alt="image" />
                    </center>
                    <Input
                        placeholder="username"
                        type="text"
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                    />
                    <Input
                        placeholder="email"
                        type="text"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />
                    <Input
                        placeholder="password"
                        type="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                    <Button type="submit" onClick={signUp}>Sign Up</Button>
                    </form>
                </div>
            </Modal>
            <Modal
                open={openSignIn}
                onClose={() => setOpenSignIn(false)}
            >
                <div style={modalStyle} className={classes.paper}>
                    <form className="app__signup">
                    <center>
                        <img className="app__headerImage" src="https://cdn.worldvectorlogo.com/logos/instagram-1.svg" alt="image" />
                    </center>
                    <Input
                        placeholder="email"
                        type="text"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />
                    <Input
                        placeholder="password"
                        type="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                    <Button type="submit" onClick={signIn}>Sign In</Button>
                    </form>
                </div>
            </Modal>
            <div className="app__header">
                <img src="https://cdn.worldvectorlogo.com/logos/instagram-1.svg" alt="image" />
                {user?
                    <Button  className="btn" onClick={() => auth.signOut()}>Logout</Button>
                    :
                    <div className="sign__container">
                    <Button  className="btn" onClick={() => setOpenSignIn(true)}>Sign In</Button>
                    <Button  className="btn" onClick={() => setOpen(true)}>Sign up</Button>
                    </div>
                }
                </div>
                {/* <hr /> */}
                <div className="app__post">
                {
                    posts.map(({ id, post }) => (
                        <Post postId={id} user={user} key={id} username={post.username} imageUrl={post.imageUrl} caption={post.caption} />
                    ))
                }
                </div>
                {user?.displayName?
            <ImageUpload username={user.displayName}/>
            :<h3>Sorry You need to log in to upload files</h3>
            }
        </div>
    );
};

export default App;
